.loader {
    height: 4px;
    width: 100%;
    position: relative;
    overflow: hidden;
}

.loader:before {
    display: block;
    position: absolute;
    content: "";
    width: 200px;
    height: 1px;
    background-color: #222;
    animation: loading 5s linear infinite;
}

@keyframes loading {
    from {
        width: 0%;
    }

    to {
        width: 100%;
    }
}

